@import url(https://fonts.googleapis.com/css2?family=Red+Hat+Display:wght@300;400;500;600;700;800;900&display=swap);
body {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  display: grid;
  align-items: center;
  justify-content: center;
  color: #f9eef9;
  text-align: center;
  background: rgb(0, 2, 40);
  background: linear-gradient(
    130deg,
    rgba(0, 2, 40, 0.9877450980392157) 0%,
    rgb(79, 32, 96) 49%,
    rgba(65, 102, 155, 1) 100%
  );
  font-family: "Red Hat Display", sans-serif;
}

body.active {
  overflow: hidden;
}

#root {
  min-height: 100vh;
  opacity: 1;
}

#root.active h1 {
  -webkit-transform: scale(0);
          transform: scale(0);
  -webkit-transform-origin: top bottom;
          transform-origin: top bottom;
  transition: 0.8s;
}

#root h1 {
  -webkit-transform: scale(1);
          transform: scale(1);
  -webkit-transform-origin: top bottom;
          transform-origin: top bottom;
  transition: 0.8s;
}

#root.active h2 {
  -webkit-transform: scale(0);
          transform: scale(0);
  -webkit-transform-origin: top bottom;
          transform-origin: top bottom;
  transition: 0.8s;
}

#root h2 {
  -webkit-transform: scale(1);
          transform: scale(1);
  -webkit-transform-origin: top bottom;
          transform-origin: top bottom;
  transition: 0.8s;
}

#root.active .search {
  -webkit-transform: scale(0);
          transform: scale(0);
  -webkit-transform-origin: top bottom;
          transform-origin: top bottom;
  transition: 0.8s;
}

#root .search {
  -webkit-transform: scale(1);
          transform: scale(1);
  -webkit-transform-origin: top bottom;
          transform-origin: top bottom;
  transition: 0.8s;
}

#root.active ul {
  -webkit-transform: scale(0);
          transform: scale(0);
  -webkit-transform-origin: top bottom;
          transform-origin: top bottom;
  transition: 0.8s;
}

#root ul {
  -webkit-transform: scale(1);
          transform: scale(1);
  -webkit-transform-origin: top bottom;
          transform-origin: top bottom;
  transition: 0.8s;
}

#root.active .closeBtn {
  display: inline-block;
  opacity: 1;
  transition: 1.5s;
}

#root.active .btn {
  -webkit-transform: scale(0);
          transform: scale(0);
  -webkit-transform-origin: bottom right;
          transform-origin: bottom right;
  transition: 0.8s;
}

.body::before {
  content: "";
  position: absolute;
  width: 300px;
  height: 300px;
  background-color: #f86f03;
  top: 20px;
  left: 0px;
  border-radius: 50% 50% 0 0;
  -webkit-transform: rotate(90deg);
          transform: rotate(90deg);
  opacity: 0.09;
  transition: 0.6s;
  z-index: -1;
}

.body.active::before {
  -webkit-transform: rotate(0);
          transform: rotate(0);
  transition: 0.6s;
  top: 0;
  left: 0;
  width: 50%;
  height: 100rem;
  background: #151415;
  z-index: -1;
  opacity: 1;
  border-radius: 0;
}

.body::after {
  content: "";
  position: absolute;
  width: 300px;
  height: 300px;
  background-color: #151415;
  bottom: 20px;
  right: 0px;
  border-radius: 50% 50% 0 0;
  -webkit-transform: rotate(-90deg);
          transform: rotate(-90deg);
  opacity: 0.09;
  transition: 0.6s;
  z-index: -1;
}

.body.active::after {
  -webkit-transform: rotate(0);
          transform: rotate(0);
  transition: 0.6s;
  bottom: 0;
  top: 0;
  right: 0;
  width: 50%;
  height: 100rem;
  opacity: 1;
  border-radius: 0;
}

h1 {
  font-size: 70px;
  padding: 0;
  margin: 0;
  font-weight: 900;
  color: transparent;
  -webkit-text-stroke: 1px #e6f973;
  background-clip: padding-box;
}

.TodoCounter {
  font-size: 20px;
  text-align: center;
  margin: 0;
  padding: 40px;
  font-weight: normal;
}

.TodoCounter span {
  font-weight: bold;
}

.search {
  width: 270px;
  height: 50px;
  border-radius: 20px;
  border: none;
  padding-left: 20px;
  font-size: 20px;
}

.search:focus {
  outline-color: #c33a76;
}

.btn {
  position: fixed;
  bottom: 15px;
  right: 20px;
  width: 70px;
  height: 70px;
  border-radius: 50%;
  border: none;
  background: #e6f973;
  font-size: 40px;
  -webkit-margin-after: 10px;
          margin-block-end: 10px;
  cursor: pointer;

  -webkit-transform: scale(1);

          transform: scale(1);
  -webkit-transform-origin: bottom right;
          transform-origin: bottom right;
  transition: 0.3s;
}

.btn-hide {
  -webkit-transform: scale(0);
          transform: scale(0);
  -webkit-transform-origin: bottom right;
          transform-origin: bottom right;
  transition: 0.3s;
}

li span {
  -webkit-margin-start: 10px;
          margin-inline-start: 10px;
  -webkit-margin-end: 15px;
          margin-inline-end: 15px;
}

.check {
  cursor: pointer;
}

.delete {
  width: 24px;
  height: 24px;
  border-radius: 50%;
  background: rgba(79, 32, 96);
  cursor: pointer;
  fill: #c33a76;
}

.delete:hover .Icon-svg {
  -webkit-transform: scale(1.3);
          transform: scale(1.3);
  transition: 0.1s ease-in;
  background: rgba(79, 32, 96);
  border-radius: 50%;
}

.check:hover .Icon-svg {
  -webkit-transform: scale(1.3);
          transform: scale(1.3);
  transition: 0.2s ease-in;
}

.Icon-svg {
  width: 24px;
  height: 24px;
  -webkit-transform: scale(1);
          transform: scale(1);
  transition: 0.2s ease-out;
}

ul {
  list-style: none;
  -webkit-padding-start: 0;
          padding-inline-start: 0;
  display: flex;
  flex-direction: column;
}

li {
  display: flex;
  align-items: center;
  justify-content: space-between;
  background-color: transparent;
  border: 2px solid #c33a76;
  height: auto;
  max-width: 355.35px;
  margin: 10px;
  border-radius: 25px;
}

p {
  font-weight: 800;
  color: #f9eef9;
  letter-spacing: 1.5px;
}

.complete {
  text-decoration: line-through;
  color: grey;
}

.spinner {
  background-image: linear-gradient(rgb(186, 66, 255) 35%, rgb(0, 225, 255));
  width: 150px;
  height: 150px;
  -webkit-animation: spinning82341 1.7s linear infinite;
          animation: spinning82341 1.7s linear infinite;
  align-self: center;
  -webkit-margin-before: 50px;
          margin-block-start: 50px;
  border-radius: 50%;
  -webkit-filter: blur(1px);
          filter: blur(1px);
  box-shadow: 0px -5px 20px 0px rgb(186, 66, 255),
    0px 5px 20px 0px rgb(0, 225, 255);
}

.spinner1 {
  background-color: rgb(79, 32, 96);
  width: 150px;
  height: 150px;
  border-radius: 50%;
  -webkit-filter: blur(10px);
          filter: blur(10px);
}

@-webkit-keyframes spinning82341 {
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes spinning82341 {
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.closeBtn {
  position: absolute;
  top: 5px;
  right: 5px;
  width: 70px;
  height: 70px;
  border-radius: 50%;
  border: none;
  color: #c33a76;
  background: transparent;
  z-index: 2;
  font-size: 40px;
  cursor: pointer;
  -webkit-transform: rotate(45deg) scale(1);
          transform: rotate(45deg) scale(1);
  transition: 0.8s ease;
}

.closeBtn:hover {
  -webkit-transform: rotate(225deg) scale(1.5);
          transform: rotate(225deg) scale(1.5);
  transition: 0.8s ease;
}

.Modal form {
  display: flex;
  flex-direction: column;
}

form label {
  font-size: 1.4rem;
  font-weight: bold;
  color: #f9eef9;
  letter-spacing: 2px;
  -webkit-margin-after: 20px;
          margin-block-end: 20px;
}

textarea {
  height: 50px;
  font-family: "Red Hat Display", sans-serif;
  font-size: 1rem;
  font-weight: 600;
  letter-spacing: 1px;
  -webkit-margin-after: 20px;
          margin-block-end: 20px;
  border: 2px solid #c33a76;
  border-radius: 4px;
  resize: none;
}

textarea:focus {
  outline: none;
}

.todoform-btns-container {
  display: flex;
  justify-content: center;
  grid-gap: 50px;
  gap: 50px;
  -webkit-margin-before: 20px;
          margin-block-start: 20px;
}

.btns {
  width: 200px;
  height: 50px;
  border: 2px solid #c33a76;
  background: transparent;
  color: #f9eef9;
  border-radius: 4px;
  font-size: 1.1rem;
  cursor: pointer;
}

.btn-clear {
  transition: 0.3s ease-out;
}

.btn-clear:hover {
  letter-spacing: 2px;
  transition: 0.3s ease-in;
  font-weight: bold;
}

.btn-add-todo {
  background-color: #c33a76;
  transition: 0.3s ease-out;
}

.btn-add-todo:hover {
  letter-spacing: 2px;
  transition: 0.3s ease-in;
  font-weight: bold;
}

#modal {
  display: flex;
  flex-direction: column;
  justify-self: center;
  position: fixed;
  top: 200px;
  z-index: 1;
  -webkit-transform: scale(0);
          transform: scale(0);
  transition: 0.5s ease-out;
  -webkit-transform-origin: top center;
          transform-origin: top center;
}

#modal.active {
  -webkit-transform: scale(1);
          transform: scale(1);
  transition: 0.5s ease-in;
  -webkit-transform-origin: top center;
          transform-origin: top center;
}

.Modal {
  background: linear-gradient(
    130deg,
    rgba(0, 2, 40, 0.9877450980392157) 0%,
    rgb(79, 32, 96) 49%,
    rgba(65, 102, 155, 1) 100%
  );
  width: 60vw;
  height: 300px;
  padding: 40px;
  border-radius: 10px;
}

