ul {
  list-style: none;
  padding-inline-start: 0;
  display: flex;
  flex-direction: column;
}

li {
  display: flex;
  align-items: center;
  justify-content: space-between;
  background-color: transparent;
  border: 2px solid #c33a76;
  height: auto;
  max-width: 355.35px;
  margin: 10px;
  border-radius: 25px;
}

p {
  font-weight: 800;
  color: #f9eef9;
  letter-spacing: 1.5px;
}

.complete {
  text-decoration: line-through;
  color: grey;
}
